<template>
  <div>
    <h2>yn-city-picker</h2>
    <ul>
      <li @click="handlePicker(1)"></li>
      <li @click="handlePicker(2)"></li>
      <li @click="handlePicker(3)"></li>
      <li @click="handlePicker(4)"></li>
      <li @click="handlePicker(5)"></li>
      <li @click="handlePicker(6)"></li>
      <li @click="handlePicker(7)"></li>
      <li @click="handlePicker(8)">()</li>
    </ul>
    <yn-city-picker
      :searchable="false"
      @pick="handlePick"
      v-model="picker1"
      :tabs="tabs1"
      :alphaBeta="getAlphaBeta()"
      :history="getHistory()"
      :hotCity="getHotCity()"
      showHistory
    ></yn-city-picker>
    <yn-city-picker
      :alphaBeta="getAlphaBeta()"
      :history="getHistory()"
      :hotCity="getHotCity()"
      showHistory
      :searchable="false"
      @pick="handlePick"
      v-model="picker2"
      :tabs="tabs2"
    ></yn-city-picker>
    <yn-city-picker
      :alphaBeta="getAlphaBeta()"
      :history="getHistory()"
      :hotCity="getHotCity()"
      :searchable="false"
      @pick="handlePick"
      v-model="picker3"
    ></yn-city-picker>
    <yn-city-picker
      @pick="handlePick"
      v-model="picker4"
      :limitedData="data.cnCitiesList"
      limited
    ></yn-city-picker>
    <yn-city-picker
      @pick="handlePick"
      v-model="picker5"
      :hotCity="getHotCity()"
      showHistory
      :history="getHistory()"
      :alphaBeta="getAlphaBeta()"
    ></yn-city-picker>
    <yn-city-picker
      :alphaBeta="getAlphaBeta()"
      :searchable="false"
      @pick="handlePick"
      v-model="picker6"
      :tabs="tabs2"
      :showHotCity="false"
    ></yn-city-picker>
    <yn-city-picker
      :search="getSearch()"
      :alphaBeta="getAlphaBeta()"
      :history="getHistory()"
      :hotCity="getHotCity()"
      showHistory
      @pick="handlePick"
      v-model="picker7"
      :parse="parse"
    ></yn-city-picker>
    <yn-city-picker
      :search="getSearch()"
      :alphaBeta="getAlphaBeta()"
      :history="getHistory()"
      :hotCity="getHotCity()"
      showHistory
      @pick="handlePick"
      v-model="picker8"
      column="3"
    ></yn-city-picker>
  </div>
</template>

<script>
const data = require("./ykb_hot_city.json").result;
export default {
  name: "YnCityPickerPage",
  data() {
    return {
      data,
      picker1: false,
      picker2: false,
      picker3: false,
      picker4: false,
      picker5: false,
      picker6: false,
      picker7: false,
      picker8: false,
      tabs1: [{ label: "", key: "mainland-china" }],
      tabs2: [{ label: "(/)", key: "overseas" }]
    };
  },
  methods: {
    parse(city, nameSpace) {
      return `${nameSpace} ${city.CityName}`;
    },
    getHistory() {
      return {
        title: "",
        params: { a: "1", b: "2" },
        action: this.getHistoryAction,
        parse: (e, params = {}) => {
          const { tab } = params;
          if (tab === "mainland-china") {
            return e.cnCitiesList;
          } else if (tab === "overseas") {
            return e.intCitiesList;
          }
        }
      };
    },
    getHistoryAction(params = {}) {
      console.log("", params);
      return new Promise((resolve, reject) => {
        console.log(reject);
        // setTimeout(() => {
        //   reject("");
        // }, 1000);
        setTimeout(() => {
          resolve(data);
        }, 1000);
      }).catch(err => {
        this.Toast(err);
        console.log(err, "");
      });
    },
    handlePick(e) {
      this.Toast(`${e.CityName}`);
    },
    handlePicker(num) {
      this[`picker${num}`] = !this[`picker${num}`];
    },
    getCityListByAlphaBeta(params = {}) {
      console.log("", params);
      return new Promise((resolve, reject) => {
        console.log(reject);
        // setTimeout(() => {
        //   reject("");
        // }, 1000);
        setTimeout(() => {
          resolve(data);
        }, 1000);
      }).catch(err => {
        this.Toast(err);
        console.log(err, "");
      });
    },
    getHotCityAction(params = {}) {
      console.log("", params);
      return new Promise((resolve, reject) => {
        // setTimeout(() => {
        //   reject("");
        // }, 1000);
        console.log(reject);
        setTimeout(() => {
          resolve(data);
        }, 1000);
      }).catch(err => {
        this.Toast(err);
        console.log(err, "");
      });
    },
    getSearchAction(params = {}) {
      console.log("", params);
      return new Promise((resolve, reject) => {
        console.log(reject);
        // setTimeout(() => {
        //   reject("");
        // }, 1000);
        setTimeout(() => {
          resolve(data);
        }, 1000);
      }).catch(err => {
        this.Toast(err);
        console.log(err, "");
      });
    },
    getSearch() {
      return {
        params: { a: "1", b: "2" },
        action: this.getSearchAction,
        parse: (e, params = {}) => {
          const { tab } = params;
          if (tab === "mainland-china") {
            return e.cnCitiesList;
          } else if (tab === "overseas") {
            return e.intCitiesList;
          }
        }
      };
    },
    getHotCity() {
      return {
        title: "",
        params: { a: "1", b: "2" },
        action: this.getHotCityAction,
        parse: (e, params = {}) => {
          const { tab } = params;
          if (tab === "mainland-china") {
            return e.cnCitiesList;
          } else if (tab === "overseas") {
            return e.intCitiesList;
          }
        }
      };
    },
    getAlphaBeta() {
      return {
        title: "",
        params: { a: "1", b: "2" },
        action: this.getCityListByAlphaBeta,
        parse: e => {
          return e.cnCitiesList;
        }
      };
    }
  }
};
</script>
